<template>
  <transition name="ab-message-fade" @after-leave="handleAfterLeave">
    <div
      :class="[
        'ab-message',
        type && !iconClass ? `ab-message--${type}` : '',
        center ? 'is-center' : '',
        showClose ? 'is-closable' : '',
        customClass
      ]"
      :style="positionStyle"
      v-show="visible"
      @mouseenter="clearTimer"
      @mouseleave="startTimer"
      role="alert">
      <i :class="iconClass" v-if="iconClass"></i>
      <i :class="typeClass" v-else></i>
      <slot>
        <p v-if="!dangerouslyUseHTMLString" class="ab-message__content">{{ message }}</p>
        <p v-else v-html="message" class="ab-message__content"></p>
      </slot>
      <i v-if="showClose" class="ab-message__closeBtn ab-icon-close" @click="close"></i>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'AbMessage',
  componentName: 'AbMessage',
  data () {
    return {
      typeMap: { // 类型
        success: 'success',
        info: 'information',
        warning: 'warning',
        error: 'danger'
      },
      visible: false, // 是否展示
      message: '', // 消息提醒内容
      duration: 3000, // 展示时长,毫秒
      type: 'info', // 默认类型
      iconClass: '', // 自定义图标的类名，会覆盖 type
      customClass: '', // 自定义类名
      onClose: null, // 关闭时的回调函数
      showClose: false, // 是否展示icon图标
      closed: false, // 关闭message
      verticalOffset: 20, // 距离顶部垂直位置
      timer: null, // 定时器
      dangerouslyUseHTMLString: false, // html片段判断
      center: false // 居中
    }
  },
  computed: {
    /**
     * 自定义图标样式,根据type返回对应的图标类名
     */
    typeClass () {
      let self = this
      return self.type && !self.iconClass
        ? `ab-message__icon ab-icon-${self.typeMap[self.type]}`
        : ''
    },

    /**
     * 距离顶部垂直位置
     */
    positionStyle () {
      let self = this
      return {
        'top': `${self.verticalOffset}px`
      }
    }
  },
  watch: {
    closed (newVal) { // 关闭message
      let self = this
      if (newVal) {
        self.visible = false
      }
    }
  },
  methods: {
    /**
     * 动画进入之后
     */
    handleAfterLeave () {
      let self = this
      self.$destroy(true) // 完全销毁
      self.$el.parentNode.removeChild(self.$el) // 从父节点中删除当前节点
    },

    /**
     * 关闭当前message
     */
    close () {
      let self = this
      self.closed = true
      if (typeof self.onClose === 'function') {
        self.onClose(self)
      }
    },

    /**
     * message关闭
     */
    clearTimer () {
      let self = this
      clearTimeout(self.timer)
    },

    /**
     * message展示
     */
    startTimer () {
      let self = this
      if (self.duration > 0) {
        if (self.type === 'info' || self.type === 'success') {
          self.timer = setTimeout(() => {
            if (!self.closed) {
              self.close()
            }
          }, 1000)
        } else {
          self.timer = setTimeout(() => {
            if (!self.closed) {
              self.close()
            }
          }, self.duration)
        }
      }
    },

    /**
     * 键盘关闭message
     */
    keydown (e) {
      let self = this
      if (e.keyCode === 27) { // esc关闭消息
        if (!self.closed) {
          self.close()
        }
      }
    }
  },
  mounted () {
    let self = this
    self.startTimer()
    document.addEventListener('keydown', self.keydown)
  },
  beforeDestroy () {
    let self = this
    document.removeEventListener('keydown', self.keydown)
  }
}
</script>
