<template>
  <button
    class="ab-button"
    @click.stop="buttonClick"
    :disabled="buttonDisabled || loading"
    :class="[
      type ? `ab-button--${type}` : '',
      buttonSize ? 'ab-button--' + buttonSize : '',
      {
        'is-radius': radius,
        'is-plain': plain,
        'is-disabled': buttonDisabled,
        'is-loading': loading,
        'is-circle': circle
      }
    ]">
    <i v-if="icon && !loading" :class="`ab-icon-${icon}`"></i>
    <i v-if="loading" class="ab-icon-loading"></i>
    <span v-if="$slots.default"><slot></slot></span>
  </button>
</template>
<script>
export default {
  name: 'AbButton',
  componentName: 'AbButton',
  inject: {
    abForm: {
      default: ''
    },
    abFormItem: {
      default: ''
    }
  },
  props: {
    type: { // 按钮类型
      default: String
    },
    radius: Boolean, // 圆角
    size: String, // 按钮大小
    loading: Boolean, // 加载状态
    plain: Boolean, // 边框按钮
    disabled: Boolean, // 按钮禁用
    icon: String, // 图标
    circle: Boolean // 圆形按钮
  },
  computed: {
    /**
     * 表单元素大小
     * @return { String } 元素大小值
     */
    _abFormItemSize () {
      let self = this
      return (self.abFormItem || {}).abFormItemSize
    },
    /**
     * 按钮大小
     * @return { String } 按钮大小值
     */
    buttonSize () {
      let self = this
      return self.size || self._abFormItemSize || (self.$ELEMENT || {}).size
    },
    /**
     * 判断按钮是否能点击
     * @return { Boolean } 判断结果
     */
    buttonDisabled () {
      let self = this
      return self.disabled || (self.abForm || {}).disabled
    }
  },
  methods: {
    /**
     * 按钮点击事件
     * @param { event } 点击元素
     */
    buttonClick (event) {
      let self = this
      self.$emit('click', event)
    }
  }
}
</script>
