import Vue from 'vue'
const isServer = Vue.prototype.$isServer // 判断Vue实例是否运行于服务器

export const on = (function () {
  if (!isServer && document.addEventListener) {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.addEventListener(event, handler, false)
      }
    }
  } else {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.attachEvent('on' + event, handler)
      }
    }
  }
})()

export const off = (function () {
  if (!isServer && document.removeEventListener) {
    return function (element, event, handler) {
      if (element && event) {
        element.removeEventListener(event, handler, false)
      }
    }
  } else {
    return function (element, event, handler) {
      if (element && event) {
        element.detachEvent('on' + event, handler)
      }
    }
  }
})()

/**
 * @description 获取当前页面最高层级
 * @returns { Number } 最大值
 */
export const getMaxZIndex = function () {
  let styleAll = [...document.all]
  let zIndex = styleAll.map(item => {
    return Number(window.getComputedStyle(item).zIndex) || 1 // Window.getComputedStyle()方法返回一个对象，该对象在应用活动样式表并解析这些值可能包含的任何基本计算后报告元素的所有CSS属性的值。 私有的CSS属性值可以通过对象提供的API或通过简单地使用CSS属性名称进行索引来访问
  })
  let maxZIndex = Math.max.apply(null, zIndex)
  return maxZIndex
}
