/**
 * Vue 实例混入方法详情，见 https://cn.vuejs.org/v2/guide/mixins.html
 * 原vue 1.x中有 $dispatch 和 $broadcast 但在 vue 2.x 中已废弃
 * 组件事件传播
 * @params { String } componentName 组件名称
 * @params { String } eventName 事件名称
 * @params { Array } params 参数
 */
function broadcast (componentName, eventName, params) {
  let self = this
  self.$children.forEach(child => { // 对子组件进行遍历
    let name = child.$options.componentName
    if (name === componentName) { // 当匹配到对应的组件名时，则去触发对应子组件的对应事件
      child.$emit.apply(child, [eventName].concat(params))
    } else { // 如果没有则递归继续查找子组件
      broadcast.apply(child, [componentName, eventName].concat([params]))
    }
  })
}
export default {
  methods: {
    /**
     * 向上事件通信（子组件向父组件传递）
     * @param { String } componentName 组件名称，用于匹配到正确的组件名
     * @param { String } eventName  事件名称，需要触发的事件
     * @param { Array } params 参数，事件触发时传递的参数
     */
    dispatch (componentName, eventName, params) {
      let self = this
      let parent = self.$parent || self.$root // 用于查找当前元素的父组件，如果没有就使用根节点
      let name = parent.$options.componentName // 取父组件的组件名用于后期匹配
      while (parent && (!name || name !== componentName)) { // 用于循环父组件，直到找到或者达到根元素
        parent = parent.$parent
        if (parent) {
          name = parent.$options.componentName
        }
      }
      if (parent) { // 当匹配到父组件时，触发父组件对应的事件函数
        parent.$emit.apply(parent, [eventName].concat(params))
      }
    },
    /**
     * 向下事件通信（父组件向子组件传递）
     * @param { String } componentName 组件名称，用于匹配到正确的组件名
     * @param { String } eventName  事件名称，需要触发的事件
     * @param { Array } params 参数，事件触发时传递的参数
     */
    broadcast (componentName, eventName, params) {
      let self = this
      broadcast.call(self, componentName, eventName, params)
    }
  }
}
