<template>
  <section class="ab-container" :class="{ 'is-vertical': isVertical }">
    <slot></slot>
  </section>
</template>
<script>
export default {
  name: 'AbContainer',
  componentName: 'AbContainer',
  props: {
    direction: { // 子元素排列方向 horizontal: 水平, vertical: 垂直
      type: String
    }
  },
  computed: {
    /**
     * 判断元素布局是不是水平
     * @return { Boolean } 判断结果
     */
    isVertical () {
      let self = this
      if (self.direction === 'vertical') { // 判断元素排列布局是不是垂直
        return true
      } else if (self.direction === 'horizontal') {
        return false
      }
      return self.$slots && self.$slots.default // 在没有设置布局类型的情况下, 判断是否存在元素; 如果存在就遍历数组判断是否存在头部和底部, 如果存在则用垂直, 不存在则用水平布局
        ? self.$slots.default.some(vnode => {
          const tag = vnode.componentOptions && vnode.componentOptions.tag
          return tag === 'ab-header' || tag === 'ab-footer'
        })
        : false
    }
  }
}
</script>
