const hasOwnProperty = Object.prototype.hasOwnProperty // 判断对象是否包含特定的自身（非继承）属性

/**
 * 判断对象是否包含特定的自身（非继承）属性
 * @param { Object } obj 判断对象
 * @param { String } key 判断属性
 * @return { Boolean } 判断结果
 */
export function hasOwn (obj, key) {
  return hasOwnProperty.call(obj, key)
}

/**
 * 更新对象的值
 * @param { Object } to 目标对象
 * @param { Object } _from 来源对象
 * @return { Object } 更新后的目标对象
 */
export function extend (to, _from) {
  for (let key in _from) {
    to[key] = _from[key]
  }
  return to
}

/**
 * 数组转对象
 * @param { Array } arr 转换数组
 * @return { Object } 转换后的对象
 */
export function toObject (arr) {
  let res = {}
  for (let i = 0; i < arr.length; i++) {
    if (arr[i]) {
      extend(res, arr[i])
    }
  }
  return res
}
