<template>
  <div class="ab-count-input">
    <ab-input
      v-model="query"
      :type="type"
      :placeholder="placeholder"
      :inputSize="inputSize"
      :disabled="disabled"
      :iconAlign="iconAlign"
      :icon="icon"
      :iconEvent="iconEvent"
      :resize="resize"
      :rows="rows"
      :autosize="autosize"
      :clearable="clearable"
      :buttonStyle="buttonStyle"
      @input="changeValue"
    ></ab-input>
    <div class="ab-count-input__box">
      <span
        :class="{'ab-count-input__red': currentLength > maxTextLength | currentLength < minTextLength}"
      >{{ currentLength }}</span>/
      <span>{{ currentMax }}</span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'AbCountInput',
  inject: {
    abForm: {
      default: ''
    },
    abFormItem: {
      default: ''
    }
  },
  props: {
    value: [String, Number], // 绑定值
    type: {
      // 输入框类型: 原生属性 和 textarea
      type: String,
      default: 'text'
    },
    placeholder: {
      // 占位文案
      type: String,
      default: '请输入'
    },
    inputSize: {
      // 输入框大小: small, middle, large
      type: String
    },
    disabled: {
      // 是否为禁用状态
      type: Boolean
    },
    iconAlign: {
      // 图标位置
      type: String
    },
    icon: {
      // 按钮图标
      type: String,
      default: 'ab-icon-search'
    },
    iconEvent: {
      // 图标点击事件
      type: Function
    },
    autosize: {
      // 自使用高度
      type: [Boolean, Object],
      default: false
    },
    resize: String, // 能否被缩放, 原生属性 none, both, horizontal, vertical
    rows: {
      // textarea 默认行数
      type: Number
    },
    clearable: {
      // 能否被清空
      type: Boolean
    },
    buttonStyle: {
      // 复合型输入框按钮样式 normal: 普通样式; inherit: 继承样式
      type: String,
      default: 'normal'
    },
    minTextLength: {
      // 最大长度
      type: Number,
      default: 0
    },
    maxTextLength: {
      // 最小长度
      type: Number,
      default: 0
    },
    validateFunction: {
      // 自定义判断长度方法
      type: Function
    }
  },
  data () {
    return {
      query: this.value
    }
  },

  watch: {
    //  监听value变化赋值，避免直接操作父组件的值
    value (newVal, oldVal) {
      let self = this
      self.query = self.value
    }
  },
  computed: {
    currentLength () {
      let self = this
      if (typeof self.validateFunction === 'function') {
        return parseInt(self.validateFunction(self.query) + 0.5, 10)
      } else {
        return self.query.length
      }
    },
    currentMin () {
      let self = this
      return self.minTextLength || 0
    },
    currentMax () {
      let self = this
      return self.maxTextLength || 0
    }
  },
  methods: {
    changeValue (val) {
      let self = this
      self.$emit('input', val)
    }
  }
}
</script>
