<template>
  <div
    :class="[
      type === 'textarea' ? 'ab-textarea' : 'ab-input',
      inputSize ? `ab-input--${inputSize}` : '',
      iconAlign ? `ab-input__icon--${iconAlign}` : '',
      {
        'is-disabled': inputDisabled,
        'ab-input-group': $slots.front || $slots.end,
        'ab-input-group--front': $slots.front,
        'ab-input-group--end': $slots.end,
        'ab-input-group--button': buttonStyle === 'inherit'
      }
    ]">
    <template v-if="type !== 'textarea'">
      <div class="ab-input-group__front" v-if="$slots.front">
        <slot name="front"></slot>
      </div>
      <input
        class="ab-input__inner"
        v-model="model"
        :type="type"
        :disabled="inputDisabled"
        :placeholder="placeholder">
      <div class="ab-input-group__end" v-if="$slots.end">
        <slot name="end"></slot>
      </div>
    </template>
    <textarea
      v-if="type === 'textarea'"
      v-model="model"
      ref="textarea"
      class="ab-textarea__inner"
      :rows="rows"
      :style="textareaStyle"
      :disabled="inputDisabled"
      :placeholder="placeholder"/>
    <div v-if="type !== 'textarea' && iconAlign" class="ab-input__icon">
      <i :class="icon" @click.stop="iconClick"></i>
    </div>
  </div>
</template>
<script>
import calcTextareaHeight from './calcTextareaHeight'
import merge from 'adbright-ui/src/utils/merge'
export default {
  name: 'AbInput',
  componentName: 'AbInput',
  data () {
    return {
      textareaCalcStyle: {}
    }
  },
  inject: {
    abForm: {
      default: ''
    },
    abFormItem: {
      default: ''
    }
  },
  props: {
    value: [String, Number], // 绑定值
    type: { // 输入框类型: 原生属性 和 textarea
      type: String,
      default: 'text'
    },
    placeholder: { // 占位文案
      type: String,
      default: '请输入'
    },
    inputSize: { // 输入框大小: small, middle, large
      type: String
    },
    disabled: { // 是否为禁用状态
      type: Boolean
    },
    iconAlign: { // 图标位置
      type: String
    },
    icon: { // 按钮图标
      type: String,
      default: 'ab-icon-search'
    },
    iconEvent: { // 图标点击事件
      type: Function
    },
    autosize: {
      type: [Boolean, Object],
      default: false
    },
    resize: String, // 能否被缩放, 原生属性 none, both, horizontal, vertical
    rows: { // textarea 默认行数
      type: Number
    },
    clearable: { // 能否被清空
      type: Boolean
    },
    buttonStyle: { // 复合型输入框按钮样式 normal: 普通样式; inherit: 继承样式
      type: String,
      default: 'normal'
    }
  },
  watch: {
    value (val) {
      let self = this
      self.$nextTick(self.resizeTextarea)
      if (self.validateEvent) {
        self.dispatch('AbFormItem', 'ab.form.change', [val])
      }
    },
    type () {
      let self = this
      self.$nextTick(() => {
        self.resizeTextarea()
      })
    }
  },
  computed: {
    model: {
      get () {
        let self = this
        return self.value === null || self.value === undefined ? '' : String(self.value)
      },
      set (val) {
        let self = this
        self.$emit('input', val)
      }
    },
    /**
     * 禁用输入框
     */
    inputDisabled () {
      let self = this
      return self.disabled || (self.abForm || {}).disabled
    },
    /**
     * 文本域配置样式
     */
    textareaStyle () {
      let self = this
      return merge({}, self.textareaCalcStyle, { resize: self.resize })
    }
  },
  methods: {
    /**
     * 图标点击事件
     */
    iconClick () {
      let self = this
      if (!self.inputDisabled && self.iconEvent) {
        self.iconEvent()
      }
    },
    /**
     * 配置文本域样式
     */
    resizeTextarea () {
      let self = this
      const { autosize, type } = this
      if (type !== 'textarea') return
      if (!autosize) {
        self.textareaCalcStyle = {
          minHeight: calcTextareaHeight(self.$refs.textarea).minHeight
        }
        return
      }
      const minRows = autosize.minRows
      const maxRows = autosize.maxRows
      self.textareaCalcStyle = calcTextareaHeight(self.$refs.textarea, minRows, maxRows)
    }
  },
  mounted () {
    let self = this
    self.resizeTextarea()
  }
}
</script>
