<template>
  <label
    class="ab-radio-button"
    :class="[
      { 'is-border': type === 'border' },
      { 'is-active': value === label },
      { 'is-disabled': isDisabled }
    ]"
    @keydown.space.stop.prevent="value = isDisabled ? value : label"
  >
    <input
      class="ab-radio-button__orig-radio"
      :value="label"
      type="radio"
      v-model="value"
      :name="name"
      @change="propChange"
      :disabled="isDisabled"
      @focus="focus = true"
      @blur="focus = false"
    >
    <span
      class="ab-radio-button__inner"
      @keydown.stop>
      <slot>{{ label }}</slot>
    </span>
  </label>
</template>
<script>
import Emitter from 'adbright-ui/src/mixins/emitter'
export default {
  name: 'AbRadioButton',
  componentName: 'AbRadioButton',
  mixins: [Emitter],
  inject: {
    abForm: {
      default: ''
    },
    abFormItem: {
      default: ''
    }
  },
  props: {
    label: {}, // 选择项值
    disabled: Boolean, // 禁用状态
    name: String, // 原生 name 属性
    type: String // 按钮类型 border: 边框类型
  },
  data () {
    return {
      focus: false
    }
  },
  computed: {
    value: {
      get () {
        let self = this
        return self._radioGroup.value
      },
      set (val) {
        let self = this
        self._radioGroup.$emit('input', val)
      }
    },
    /**
     * 判断是不是单选组
     * @return { Object } 判断结果
     */
    _radioGroup () {
      let self = this
      let parent = self.$parent
      while (parent) {
        if (parent.$options.componentName !== 'AbRadioGroup') {
          parent = parent.$parent
        } else {
          return parent
        }
      }
      return false
    },
    /**
     * 判断是否为禁用态
     * @return { Boolean } 判断结果
     */
    isDisabled () { // 先判断当前组件或者全表单是否为禁用态，再判断是否存在单选组并且单选组已禁用
      let self = this
      return (self.disabled || (self.abForm || {}).disabled) || (self.isGroup && self._radioGroup.disabled)
    }
  },
  methods: {

    /**
     * 绑定 change 事件
     */
    propChange () {
      let self = this
      self.$nextTick(() => {
        self.dispatch('AbRadioGroup', 'propChange', self.value)
      })
    }
  }
}
</script>
