<template>
  <div
    class="ab-switch"
    :class="{ 'is-disabled': switchDisabled, 'is-checked': checked }"
    role="switch"
    :aria-checked="checked"
    :aria-disabled="switchDisabled"
    @click.prevent="switchValue"
  >
    <input
      class="ab-switch__input"
      type="checkbox"
      @change="handleChange"
      ref="input"
      :id="id"
      :name="name"
      :disabled="switchDisabled"
      @keydown.enter="switchValue"
    >
    <span class="ab-switch__core" ref="core" :style="{ 'width': coreWidth + 'px' }">
      <span class="ab-switch__core--on" v-if="activeText && value === activeValue">{{activeText}}</span>
      <span class="ab-switch__core--off" v-if="inactiveText && value === inactiveValue">{{inactiveText}}</span>
    </span>
  </div>
</template>
<script>
import emitter from 'adbright-ui/src/mixins/emitter'

export default {
  name: 'AbSwitch',
  mixins: [emitter],
  inject: {
    abForm: {
      default: ''
    }
  },
  props: {
    value: {
      type: [Boolean, String, Number],
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    width: {
      type: Number,
      default: 48
    },
    activeText: String,
    inactiveText: String,
    activeColor: {
      type: String,
      default: ''
    },
    inactiveColor: {
      type: String,
      default: ''
    },
    activeValue: {
      type: [Boolean, String, Number],
      default: true
    },
    inactiveValue: {
      type: [Boolean, String, Number],
      default: false
    },
    name: {
      type: String,
      default: ''
    },
    validateEvent: {
      type: Boolean,
      default: true
    },
    id: String
  },
  data () {
    return {
      coreWidth: this.width
    }
  },
  created () {
    if (!~[this.activeValue, this.inactiveValue].indexOf(this.value)) {
      this.$emit('input', this.inactiveValue)
    }
  },
  computed: {
    checked () {
      return this.value === this.activeValue
    },
    switchDisabled () {
      return this.disabled || (this.abForm || {}).disabled
    }
  },
  watch: {
    checked () {
      this.$refs.input.checked = this.checked
      if (this.activeColor || this.inactiveColor) {
        this.setBackgroundColor()
      }
      if (this.validateEvent) {
        this.dispatch('AbFormItem', 'ab.form.change', [this.value])
      }
    }
  },
  methods: {
    handleChange (event) {
      const val = this.checked ? this.inactiveValue : this.activeValue
      this.$emit('input', val)
      this.$emit('change', val)
      this.$nextTick(() => {
        this.$refs.input.checked = this.checked
      })
    },
    setBackgroundColor () {
      let newColor = this.checked ? this.activeColor : this.inactiveColor
      this.$refs.core.style.borderColor = newColor
      this.$refs.core.style.backgroundColor = newColor
    },
    switchValue () {
      !this.switchDisabled && this.handleChange()
    }
  },
  mounted () {
    /* istanbul ignore if */
    this.coreWidth = this.width || 48
    if (this.activeColor || this.inactiveColor) {
      this.setBackgroundColor()
    }
    this.$refs.input.checked = this.checked
  }
}
</script>
