<template>
  <div
    class="ab-step"
    :class="[
      { 'is-pass': model > index },
      { 'is-now': model === index },
      { 'is-success': isSuccess },
      { 'is-error': isError },
      { 'is-click': $parent.isClick && clickabled },
      isLast && !space && 'is-flex',
      `is-${$parent.direction}`,
      { 'is-line': isLine }
    ]"
    :style="styles">
    <div class="ab-step__head">
      <div class="ab-step__head--line"></div>
      <div class="ab-step__head--icon" @click.stop="changeActive('icon')">
        <i v-if="isSuccess" class="ab-icon-check"></i>
        <i v-else-if="isError" class="ab-icon-close"></i>
        <span v-else>{{ index + 1 }}</span>
      </div>
    </div>
    <div class="ab-step__main">
      <div
        class="ab-step__main--title"
        :class="[
          { 'is-titleclick': isLine && $parent.isClick && clickabled }
        ]"
        @click.stop="changeActive('title')">
        {{ title || statusText }}
      </div>
      <div class="ab-step__main--content">{{ content }}</div>
    </div>
  </div>
</template>
<script>
import Emitter from 'adbright-ui/src/mixins/emitter'
export default {
  name: 'AbStep',
  componentName: 'AbStep',
  mixins: [Emitter],
  data () {
    return {
      index: -1
    }
  },
  computed: {
    /**
     * 获取父组件
     * @return { Boolean } 判断结果
     */
    getSteps () {
      let self = this
      let parent = self.$parent
      while (parent) { // 向上循环查询父组件
        if (parent.$options.componentName !== 'AbSteps') { // 如果父组件不为步骤条则向上查询
          parent = parent.$parent
        } else { // 若为单选组则该组件属于该组组件，退出循环
          self._steps = parent
          return true
        }
      }
      return false
    },
    /**
     * 操作绑定值
     */
    model: {
      get () {
        let self = this
        return self.getSteps ? self._steps.value : ''
      },
      set (val) {
        let self = this
        if (self.getSteps) {
          self.dispatch('AbSteps', 'input', [val])
        }
      }
    },
    /**
     * 计算挂载步骤元素总个数
     * @return { Number } 总数
     */
    total () {
      let self = this
      return self.$parent.steps.length
    },
    /**
     * 自定义间距
     */
    space () {
      let self = this
      const { $parent: { space } } = self
      return space
    },
    /**
     * 判断是不是为最后一个挂载元素
     * @return { Boolean } 判断结果
     */
    isLast () {
      let self = this
      return self.index === self.total - 1
    },
    /**
     * 判断状态是否为成功
     * @return { Boolean } 判断结果
     */
    isSuccess () {
      let self = this
      return self.isStatus && (self.status === 'success' || self.model > self.index || (self.model === self.index && self.$parent.status === 'success'))
    },
    /**
     * 判断状态是否为错误
     * @return { Boolean } 判断结果
     */
    isError () {
      let self = this
      return self.isStatus && (self.status === 'error' || (self.model === self.index && self.$parent.status === 'error'))
    },
    /**
     * 判断是不是行步骤
     * @return { Boolean } 判断结果
     */
    isLine () {
      let self = this
      return self.$parent.isLine
    },
    /**
     * 判断是否展示状态
     * @return { Boolean } 判断结果
     */
    isStatus () {
      let self = this
      return self.$parent.isStatus
    },
    /**
     * 判断是不是垂直排列
     */
    isVertical () {
      let self = this
      return self.$parent.direction === 'vertical'
    },
    /**
     * 各状态对应展示文案
     * @return { String } 对应展示文案
     */
    statusText () {
      let self = this
      let text = ''
      if (!self.isStatus) return text
      if (self.isSuccess) {
        text = '已完成'
      } else if (self.isError) {
        text = '失败'
      } else if (self.model === self.index) {
        text = '进行中'
      } else {
        text = '未进行'
      }
      return text
    },
    /**
     * 绑定 行内样式
     */
    styles () {
      let self = this
      const space = (typeof self.space === 'number'
        ? self.space + 'px'
        : self.space
          ? self.space
          : `${100 / (self.total - 1)}%`)
      let style = {}
      if (self.isLine) return style // 如果为行步骤不设置行内样式
      if (self.isVertical) { // 垂直排列样式
        style.flexBasis = space
        return style
      }
      if (self.isLast) { // 如果为最后一个元素
        style.maxWidth = 100 / self.total + '%'
      } else {
        style.flexBasis = space
      }
      return style
    }
  },
  props: {
    title: String, // 展示步骤标题
    content: String, // 描述文本
    status: String, // 当前步骤状态 success: 成功; error: 失败
    clickabled: { // 能否被点击
      type: Boolean,
      default: true
    }
  },
  methods: {
    /**
     * 点击跳转对应步骤
     * @param { String } type 点击类型
     */
    changeActive (type) {
      let self = this
      if (type === 'title' && !self.isLine) return false // 如果点击是文本点击，并且不是行步骤不触发跳转事件
      if (self.$parent.isClick && self.clickabled) { // 只有配置点击步骤，并且当前步骤可点击的时候才执行跳转事件
        self.model = self.index
      }
    }
  },
  beforeCreate () { // 在元素产生前向父元素挂载元素
    let self = this
    self.$parent.steps.push(self)
  },
  beforeDestroy () { // 在元素删除前向父元素注销元素
    let self = this
    const steps = self.$parent.steps
    const index = steps.indexOf(self) // 获取元素在父元素挂载时的索引
    if (index >= 0) { // 如果父元素存在该元素挂载则注销该元素
      steps.splice(index, 1)
    }
  }
}
</script>
