export default {
  name: 'AbRow',
  componentName: 'AbRow',
  props: {
    tag: { // 自定义元素标签
      type: String,
      default: 'div'
    },
    gutter: Number, // 栅格间隔
    type: String, // 布局模式
    justify: { // flex 布局下的水平排列方式
      type: String,
      default: 'start'
    },
    align: { // flex 布局下的垂直排列方式
      type: String,
      default: 'top'
    }
  },
  computed: {
    style () {
      let self = this
      const ret = {}
      if (self.gutter) { // 如果存在分栏偏移则需调整间距左右拉开
        ret.marginLeft = `-${self.gutter / 2}px`
        ret.marginRight = ret.marginLeft
      }
      return ret
    }
  },
  render (h) {
    let self = this
    return h(self.tag, {
      class: [
        'ab-row',
        self.justify !== 'start' ? `is-justify-${self.justify}` : '',
        self.align !== 'top' ? `is-align-${self.align}` : '',
        { 'ab-row--flex': self.type === 'flex' }
      ],
      style: self.style
    }, self.$slots.default)
  }
}
