/**
 * 对象合并
 */
export default function (target) {
  for (let i = 1, j = arguments.length; i < j; i++) {
    let obj = arguments[i] || {}
    for (let prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        let value = obj[prop]
        if (value !== undefined) {
          target[prop] = value
        }
      }
    }
  }
  return target
}
