<template>
  <div class="eg">
    <h2>Steps 步骤条</h2>
    <h3>基本用法 Basic</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value1">
          <ab-step title="步骤一"/>
          <ab-step title="步骤二"/>
          <ab-step title="步骤三"/>
        </ab-steps>
      </div>
    </div>
    <h3>状态步骤条 Status</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value2" is-status>
          <ab-step v-for="(item, index) in 5" :key="index" :title="index === 3 ? '步骤4' : ''"/>
        </ab-steps>
        <ab-button type="primary" @click.stop="nextStep2">下一步</ab-button>
      </div>
    </div>
    <h3>描述步骤条 Description</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value3">
          <ab-step title="步骤 1" content="犹豫，就会败北"/>
          <ab-step title="步骤 2" content="果断，就会白给"/>
          <ab-step title="步骤 3" content="群主，女装傀儡"/>
          <ab-step title="步骤 4" content="正字，写满大腿"/>
        </ab-steps>
        <ab-button type="primary" @click.stop="nextStep3">下一步</ab-button>
      </div>
    </div>
    <h3>自定义行距 Space</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value4" :space="100">
          <ab-step title="步骤一"/>
          <ab-step title="步骤二"/>
          <ab-step title="步骤三"/>
        </ab-steps>
      </div>
    </div>
    <h3>竖直步骤条 Vertical</h3>
    <div class="eg-box">
      <div class="source">
        <div style="height: 300px;">
          <ab-steps v-model="value5" direction="vertical">
            <ab-step title="步骤一"/>
            <ab-step title="步骤二"/>
            <ab-step title="步骤三" content="这是一段描述文字"/>
          </ab-steps>
        </div>
      </div>
    </div>
    <h3>点击步骤 Click</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value6" is-click>
          <ab-step title="步骤一"/>
          <ab-step title="步骤二"/>
          <ab-step title="步骤三"/>
          <ab-step title="步骤四"/>
        </ab-steps>
      </div>
    </div>
    <h3>行步骤 Line</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value7" is-click is-line>
          <ab-step title="步骤一"/>
          <ab-step title="步骤二"/>
          <ab-step title="步骤三" :clickabled="false"/>
          <ab-step title="步骤四"/>
        </ab-steps>
      </div>
    </div>
    <h3>步骤错误提示 Status</h3>
    <div class="eg-box">
      <div class="source">
        <ab-steps v-model="value8" is-status status="error">
          <ab-step/>
          <ab-step/>
          <ab-step/>
          <ab-step/>
        </ab-steps>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'egSteps',
  data () {
    return {
      value1: 1,
      value2: 1,
      value3: 1,
      value4: 1,
      value5: 1,
      value6: 1,
      value7: 1,
      value8: 1
    }
  },
  methods: {
    /**
     * 状态步骤条下一步事件
     */
    nextStep2 () {
      let self = this
      if (self.value2 > 4) {
        self.value2 = 0
      } else {
        self.value2++
      }
    },
    /**
     * 描述步骤条下一步事件
     */
    nextStep3 () {
      let self = this
      if (self.value3 > 2) {
        self.value3 = 0
      } else {
        self.value3++
      }
    }
  }
}
</script>
