<template>
  <div class="eg">
    <h2>Message 消息提示</h2>
    <h3>基础用法</h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="openmessage">打开消息提示</ab-button>
          <ab-button @click="openVn">VNode</ab-button>
        </div>
      </div>
    </div>
    <h3>不同状态</h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="opensuccess">成功</ab-button>
          <ab-button @click="openwarning">警告</ab-button>
          <ab-button @click="openmessage">消息</ab-button>
          <ab-button @click="openerror">错误</ab-button>
        </div>
      </div>
    </div>
    <h3>可关闭的</h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="opensuccessClose">成功</ab-button>
          <ab-button @click="openwarningClose">警告</ab-button>
          <ab-button @click="openmessageClose">消息</ab-button>
          <ab-button @click="openerrorClose">错误</ab-button>
        </div>
      </div>
    </div>
    <h3>文字居中</h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="openCenter">文字居中</ab-button>
        </div>
      </div>
    </div>
    <h3>HTML片段</h3>
    <div class="eg-box">
      <div class="source">
        <div class="eg-message">
          <ab-button @click="openHtml">HTML片段</ab-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'egMessage',
  methods: {
    /**
     * 打开消息提示
     */
    openmessage () {
      let self = this
      self.$message({
        message: '这是一条普通的消息提示'
      })
    },

    /**
     * 打开VNode
     */
    openVn () {
      let self = this
      const h = self.$createElement
      self.$message({
        message: h('p', null, [
          h('span', null, '内容可以是 '),
          h('i', { style: 'color: teal' }, 'VNode')
        ])
      })
    },

    /**
     * 打开成功消息提示
     */
    opensuccess () {
      let self = this
      self.$message({
        message: '这是一条成功的消息提示',
        type: 'success'
      })
    },

    /**
     * 打开警告消息提示
     */
    openwarning () {
      let self = this
      self.$message({
        message: '这是一条警告的消息提示',
        type: 'warning'
      })
    },

    /**
     * 打开错误提示
     */
    openerror () {
      let self = this
      self.$message({
        message: '这是一条错误的消息提示',
        type: 'error'
      })
    },

    /**
     * 打开成功消息提示（带关闭按钮）
     */
    opensuccessClose () {
      let self = this
      self.$message({
        message: '这是一条成功的消息提示',
        type: 'success',
        showClose: true
      })
    },

    /**
     * 打开警告消息提示（带关闭按钮）
     */
    openwarningClose () {
      let self = this
      self.$message({
        message: '这是一条警告的消息提示',
        type: 'warning',
        showClose: true
      })
    },

    /**
     * 打开错误消息提示（带关闭按钮）
     */
    openerrorClose () {
      let self = this
      self.$message({
        message: '这是一条错误的消息提示',
        type: 'error',
        showClose: true
      })
    },

    /**
     * 打开普通消息提示（带关闭按钮）
     */
    openmessageClose () {
      let self = this
      self.$message({
        message: '这是一条普通的消息提示',
        showClose: true
      })
    },

    /**
     * 打开消息提示（居中）
     */
    openCenter () {
      let self = this
      self.$message({
        message: '居中',
        center: true
      })
    },

    /**
     * 打开消息提示（Html片段）
     */
    openHtml () {
      let self = this
      self.$message({
        message: '<i>HTML</i> <strong>片段</strong>',
        dangerouslyUseHTMLString: true
      })
    }
  }
}
</script>
<style lang="scss">
  .eg-message {
    margin-bottom: 15px;
    width: 480px;
    &:last-child {
      margin-bottom: 0;
    }
  }
</style>
