<template>
  <label
    class="ab-radio"
    :class="[
      { 'is-disabled': isDisabled },
      { 'is-checked': model === label }
    ]"
    @keydown.space.stop.prevent="model = isDisabled ? model : label">
    <span
      class="ab-radio__input"
      :class="{
        'is-disabled': isDisabled,
        'is-checked': model === label
      }">
      <span class="ab-radio__inner"></span>
      <input
        class="ab-radio__box"
        :value="label"
        type="radio"
        :disabled='isDisabled'
        v-model="model"
        @focus="focus = true"
        @blur="focus = false"
        @change="propChange"
        >
    </span>
    <span class="ab-radio__label" @keydown.stop><slot>{{ label }}</slot></span>
  </label>
</template>
<script>
import Emitter from 'adbright-ui/src/mixins/emitter'
export default {
  name: 'AbRadio',
  componentName: 'AbRadio',
  mixins: [Emitter],
  inject: {
    abForm: {
      default: ''
    },
    abFormItem: {
      default: ''
    }
  },
  data () {
    return { // 判断是否为聚焦
      focus: false
    }
  },
  computed: {
    /**
     * 判断是否为单选组
     * @return { Boolean } 判断结果
     */
    isGroup () {
      let self = this
      let parent = self.$parent
      while (parent) { // 向上循环查询父组件
        if (parent.$options.componentName !== 'AbRadioGroup') { // 如果父组件不为单选组则向上查询
          parent = parent.$parent
        } else { // 若为单选组则该组件属于该组组件，退出循环
          self._radioGroup = parent
          return true
        }
      }
      return false
    },
    /**
     * 判断是否为选中值
     */
    model: {
      get () {
        let self = this
        return self.isGroup ? self._radioGroup.value : self.value
      },
      set (val) {
        let self = this
        if (self.isGroup) {
          self.dispatch('AbRadioGroup', 'input', [val])
        } else {
          self.$emit('input', val)
        }
      }
    },
    /**
     * 判断是否为禁用态
     * @return { Boolean } 判断结果
     */
    isDisabled () { // 先判断当前组件或者全表单是否为禁用态，再判断是否存在单选组并且单选组已禁用
      let self = this
      return (self.disabled || (self.abForm || {}).disabled) || (self.isGroup && self._radioGroup.disabled)
    }
  },
  props: {
    value: {}, // 选中值
    disabled: { // 是否可选
      type: Boolean,
      default: false
    },
    label: { // 单选框展示文案
      type: [String, Number]
    }
  },
  methods: {

    /**
     * 绑定 change 事件
     */
    propChange () {
      let self = this
      self.$nextTick(() => {
        self.$emit('change', self.model)
        self.isGroup && self.dispatch('ABRadioGroup', 'propChange', self.model)
      })
    }
  }
}
</script>
