<template>
  <div class="ab-progress">
    <div
      v-if="type === 'line'"
      class="ab-progress__line"
      :class="{
        'is-success': status === 'success',
        'is-error': status === 'error'
      }">
      <div class="ab-progress__line--text">
        <slot></slot>
        <span class="ab-progress__line--value">
          <i v-if="status === 'success'" class="ab-icon-success"></i>
          <i v-else-if="status === 'error'" class="ab-icon-danger"></i>
          <span v-else>{{ model }}%</span>
        </span>
      </div>
      <div class="ab-progress__line--bar" :style="{ height: `${lineBar}px` }">
        <div class="ab-progress__line--inner" :style="barStyle"></div>
      </div>
    </div>
    <div
      v-if="type === 'circle'"
      class="ab-progress__circle"
      :class="{
        'is-success': status === 'success',
        'is-error': status === 'error'
      }"
      :style="{
        height: `${width}px`,
        width: `${width}px`
      }">
      <svg viewBox="0 0 100 100">
        <path class="ab-progress__circle--track" :d="trackPath" stroke="#e5e9f2" :stroke-width="relativeLineWeight" fill="none"></path>
        <path class="ab-progress__circle--path" :d="trackPath" stroke-linecap="round" :stroke="stroke" :stroke-width="relativeLineWeight" fill="none" :style="circlePathStyle"></path>
      </svg>
      <span
        class="ab-progress__circle--value"
        :style="{
          fontSize: `${fontSize}px`
        }">
        <i v-if="status === 'success'" class="ab-icon-check"></i>
        <i v-else-if="status === 'error'" class="ab-icon-close"></i>
        <span v-else-if="showValue">{{ model }}%</span>
      </span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'AbProgress',
  componentName: 'AbProgress',
  computed: {
    /**
     * 进度值计算属性
     */
    model: {
      get () {
        let self = this
        let val = Number(self.value)
        return isNaN(val) ? 0 : val < 0 ? 0 : val > 100 ? 100 : val
      },
      set (val) {
        let self = this
        self.$emit('input', val)
      }
    },
    /**
     * 进度条计算样式
     */
    barStyle () {
      let self = this
      const style = {}
      style.width = `${self.model}%`
      return style
    },
    /**
     * 计算线型进度条高度
     */
    lineBar () {
      let self = this
      let val = self.lineWeight
      return val ? isNaN(val) ? val.indexOf('px') ? val.replace('px', '') : 2 : Number(val) > 0 ? val : 2 : 2
    },
    /**
     * 相对路径宽度
     */
    relativeLineWeight () {
      let self = this
      return (self.lineBar / self.width * 100).toFixed(1)
    },
    /**
     * 绘图路径
     */
    trackPath () {
      let self = this
      const radius = parseInt(50 - parseFloat(self.relativeLineWeight) / 2, 10)
      return `M 50 50 m 0 -${radius} a ${radius} ${radius} 0 1 1 0 ${radius * 2} a ${radius} ${radius} 0 1 1 0 -${radius * 2}`
    },
    /**
     * 百分比计算路径
     */
    perimeter () {
      let self = this
      const radius = 50 - parseFloat(self.relativeLineWeight) / 2
      return 2 * Math.PI * radius
    },
    circlePathStyle () {
      let self = this
      const perimeter = self.perimeter
      return {
        strokeDasharray: `${perimeter}px,${perimeter}px`,
        strokeDashoffset: (1 - self.model / 100) * perimeter + 'px',
        transition: 'stroke-dashoffset 0.6s ease 0s, stroke 0.6s ease'
      }
    },
    stroke () {
      let self = this
      let ret
      switch (self.status) {
        case 'success':
          ret = '#14CD64'
          break
        case 'error':
          ret = '#FF504B'
          break
        default:
          ret = '#1482FF'
      }
      return ret
    }
  },
  props: {
    type: { // 展示类型默认为 line
      type: String,
      default: 'line',
      validator: val => ['line', 'circle'].indexOf(val) > -1 // 只支持两种类型 line、circle
    },
    value: { // 绑定进度值
      type: [Number, String],
      default: 0
    },
    status: { // 绑定状态 success: 成功; error: 失败;
      type: String,
      default: ''
    },
    lineWeight: { // 线型进度条高度
      type: [Number, String]
    },
    width: { // 进度条画布宽高
      type: Number,
      default: 34
    },
    showValue: { // 是否展示圆环进度条进度文案
      type: Boolean
    },
    fontSize: { // 圆环进度条进度文案字体大小
      type: Number,
      default: 12
    }
  }
}
</script>
