<template>
  <span
    class="ab-tag"
    :class="[
      { 'is-message': status === 'message' },
      { 'is-success': status === 'success' },
      { 'is-warning': status === 'warning' },
      { 'is-danger': status === 'danger' },
      { 'is-end': status === 'end' },
      { 'is-dot': type === 'dot' || type === 'borderdot' },
      { 'is-borderdot': type === 'borderdot' }
    ]">
    <i v-if="type === 'dot' || type === 'borderdot'" class="ab-tag__dot"></i>
    <slot></slot>
  </span>
</template>
<script>
export default {
  name: 'AbTag',
  componentName: 'AbTag',
  props: {
    status: String, // tag 状态 message: 通知; success: 成功; warning: 警告; danger: 危险; end: 结束;
    type: String // tag 类型 normal: 普通; dot: 点; borderdot: 边框圆点;
  }
}
</script>
