export default {
  name: 'AbCol',
  props: {
    span: { // 栅格占据的列数
      type: Number,
      default: 24
    },
    tag: { // 自定义元素标签
      type: String,
      default: 'div'
    },
    offset: Number, // 栅格左侧的间隔格数
    pull: Number, // 栅格向左移动格数
    push: Number, // 栅格右侧的间隔格数
    xs: [Number, Object], // < 768px 响应式栅格数或者栅格树属性对象
    sm: [Number, Object], // ≥ 768px 响应式栅格数或者栅格树属性对象
    md: [Number, Object], // ≥ 992px 响应式栅格数或者栅格树属性对象
    lg: [Number, Object], // ≥ 1200px 响应式栅格数或者栅格树属性对象
    xl: [Number, Object] // ≥ 1920px 响应式栅格数或者栅格树属性对象
  },

  computed: {
    /**
     * 获取分栏间隔值
     * @param { Number } 分栏间隔值
     */
    gutter () {
      let parent = this.$parent
      while (parent && parent.$options.componentName !== 'AbRow') { // 从父级查找组件名为 'AbRow' 的元素, 若无则查找其父级
        parent = parent.$parent
      }
      return parent.gutter ? parent.gutter : 0 // 判断是否存在 gutter 这个字段, 是则返回该字段的值, 否则返回 0
    }
  },
  /**
   * 渲染栅格
   * @param { Function } h 渲染方法
   */
  render (h) {
    let classList = []
    let style = {}
    if (this.gutter) { // 判断是否存在分栏间隔
      style.paddingLeft = this.gutter / 2 + 'px' // 内填充等于 间隔值 / 2 达到左右对称实现真实间隔
      style.paddingRight = style.paddingLeft // 左右填充值相等
    }
    let label = ['span', 'offset', 'pull', 'push'] // 四个配置元素偏移的配置
    label.forEach(prop => { // 遍历配置
      if (this[prop] || this[prop] === 0) { // 如果存在该配置 或者 该配置等于 0
        classList.push(
          prop !== 'span'
            ? `ab-col-${prop}-${this[prop]}`
            : `ab-col-${this[prop]}`
        )
      }
    })
    let config = ['xs', 'sm', 'md', 'lg', 'xl']
    config.forEach(size => { // 配置对应的断点下的栅格
      if (typeof this[size] === 'number') {
        classList.push(`ab-col-${size}-${this[size]}`)
      } else if (typeof this[size] === 'object') {
        let props = this[size]
        Object.keys(props).forEach(prop => {
          classList.push(
            prop !== 'span'
              ? `ab-col-${size}-${prop}-${props[prop]}`
              : `ab-col-${size}-${props[prop]}`
          )
        })
      }
    })
    return h(this.tag, { // 渲染该元素条件渲染的样式列表
      class: ['ab-col', classList],
      style // 赋值样式
    }, this.$slots.default)
  }
}
