import Vue from 'vue'
import Main from './message.vue'
import { isVNode } from 'adbright-ui/src/utils/vdom'
import { getMaxZIndex } from 'adbright-ui/src/utils/dom'
let MessageConstructor = Vue.extend(Main) // 直接将Vue组件作为Vue.extend的参数
let instance
let instances = [] // 存放当前未close的message
let seed = 1
let zIndex = 2000
const Message = function (options) {
  zIndex = getMaxZIndex()
  if (Vue.prototype.$isServer) return
  options = options || {}
  if (typeof options === 'string') { // 如果只传入字符串，将其设置为显示的信息
    options = {
      message: options // 这里的message就是message.vue中data中的message
    }
  }
  // userOnClose用来存放用户设置关闭时的回调函数, 参数为被关闭的 message 实例
  let userOnClose = options.onClose
  let id = 'message_' + seed++

  options.onClose = function () {
    Message.close(id, userOnClose)
  }
  Message.closeAll()
  instance = new MessageConstructor({ // 实例化一个带有content内容的instance
    data: options // 在这里将传过来的参数匹配到message.vue组件的data
  })
  instance.id = id
  // 判断instance.message是不是虚拟节点
  if (isVNode(instance.message)) {
    instance.$slots.default = [instance.message]
    instance.message = null
  }
  instance.$mount() // 挂载但是并未插入dom，是一个完整的Vue实例
  document.body.appendChild(instance.$el) // 将dom插入body
  instance.verticalOffset = options.offset || 20
  instance.visible = true // 这里修改message.vue数据中的visible,这样message组件展示
  instance.$el.style.zIndex = zIndex++ // zindex+1，保证能盖在之前的上面
  instances.push(instance)
  return instance
}

let messageList = ['success', 'warning', 'info', 'error']
messageList.forEach(type => { // 同element-ui一样，在给Message绑定四个方法，直接调用
  Message[type] = options => {
    if (typeof options === 'string') {
      options = {
        message: options
      }
    }
    options.type = type
    return Message(options)
  }
})

Message.close = function (id, userOnClose) { // 组件的close方法中调用onClose再调该方法
  let len = instances.length
  let index = -1
  for (let i = 0; i < len; i++) {
    if (id === instances[i].id) { // 通过id找到该message实例
      index = i
      if (typeof userOnClose === 'function') {
        userOnClose(instances[i])
      }
      instances.splice(i, 1) // 移除message实例
      break
    }
  }
  if (len <= 1 || index === -1 || index > instances.length - 1) return
  const removedHeight = instances[index].$el.offsetHeight
  for (let i = index; i < len - 1; i++) {
    let dom = instances[i].$el
    dom.style['top'] =
      parseInt(dom.style['top'], 10) - removedHeight - 16 + 'px'
  }
}

Message.closeAll = function () { // 关闭所有的消息提示弹窗
  for (let i = instances.length - 1; i >= 0; i--) {
    instances[i].close()
  }
}

export default Message
