<template>
  <div
    class="ab-radio-group"
    :class="{'is-vertical': vertical}">
    <slot></slot>
  </div>
</template>
<script>
import Emitter from 'adbright-ui/src/mixins/emitter'
export default {
  name: 'AbRadioGroup',
  componentName: 'AbRadioGroup',
  inject: {
    abFormItem: {
      default: ''
    }
  },
  mixins: [Emitter],
  props: {
    value: {}, // 绑定值
    disabled: Boolean, // 是否禁用
    vertical: Boolean // 是否垂直排列
  },
  created () { // 组件产生时的钩子
    let self = this
    self.$on('propChange', val => { // 触发子组件 change 事件时触发 change 事件
      self.$emit('change', val)
    })
  },
  watch: {
    value (val) {
      let self = this
      self.dispatch('AbFormItem', 'ab.form.change', [self.value])
    }
  }
}
</script>
