<template>
  <div
    class="ab-steps"
    :class="[
       `ab-steps--${direction}`
    ]">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'AbSteps',
  componentName: 'AbSteps',
  data () {
    return {
      steps: [], // 挂载的步骤元素
      stepOffset: 0
    }
  },
  watch: {
    steps (val) { // 监听挂载元素变化，重新赋予索引值
      val.forEach((child, index) => {
        child.index = index
      })
    }
  },
  props: {
    space: [Number, String], // 间隔空间
    value: {}, // 当前步骤
    direction: { // 排列方式
      type: String,
      default: 'horizontal'
    },
    isStatus: Boolean, // 是否状态展示
    status: String, // 当前步骤状态 success: 成功; error: 失败
    isClick: Boolean, // 是否点击跳转对应步骤
    isLine: Boolean // 是否为行文本样式
  }
}
</script>
